      //   'a8_1': '0.00', 'a8_3': '0.00',
      //   'a10_1': '0.00', 'a10_2': '0.00', 'a10_3': '0.00', 'a10_4': '0.00',
      //   'a11_1': '0.00', 'a11_2': '0.00', 'a11_3': '0.00', 'a11_4': '0.00',
      //   'a12_1': '0.00', 'a12_2': '0.00', 'a12_3': '0.00', 'a12_4': '0.00',
      //   'a13_1': '0.00', 'a13_2': '0.00', 'a13_3': '0.00', 'a13_4': '0.00',
      //   'a14_1': '0.00', 'a14_2': '0.00', 'a14_3': '0.00', 'a14_4': '0.00',
      //   'a15_1': '0.00', 'a15_2': '0.00', 'a15_3': '0.00', 'a15_4': '0.00',
      //   'a16_1': '0.00', 'a16_2': '0.00', 'a16_3': '0.00', 'a16_4': '0.00',
      //   'a17_1': '0.00', 'a17_2': '0.00', 'a17_3': '0.00', 'a17_4': '0.00',
      //   'a18_1': '0.00', 'a18_2': '0.00', 'a18_3': '0.00', 'a18_4': '0.00',
      //   'a19_1': '0.00', 'a19_2': '0.00', 'a19_3': '0.00', 'a19_4': '0.00',
      //   'a21_1': '0.00', 'a21_2': '0.00', 'a21_3': '0.00', 'a21_4': '0.00'
      // },
      form: {
        'a1_2': '0.00',
        // 'a1_1': '0.00', 'a1_2': '0.00', 'a1_3': '0.00', 'a1_4': '0.00',
        // 'a2_1': '0.00', 'a2_2': '0.00', 'a2_3': '0.00', 'a2_4': '0.00',
        // 'a3_1': '0.00', 'a3_2': '0.00', 'a3_3': '0.00', 'a3_4': '0.00',
        // 'a4_2': '0.00', 'a4_4': '0.00',
        // 'a5_2': '0.00', 'a5_4': '0.00',
        // 'a6_2': '0.00', 'a6_4': '0.00',
        // 'a7_1': '0.00', 'a7_3': '0.00',
        // 'a8_1': '0.00', 'a8_3': '0.00',
        // 'a10_1': '0.00', 'a10_2': '0.00', 'a10_3': '0.00', 'a10_4': '0.00',
        // 'a11_1': '0.00', 'a11_2': '0.00', 'a11_3': '0.00', 'a11_4': '0.00',
        // 'a12_1': '0.00', 'a12_2': '0.00', 'a12_3': '0.00', 'a12_4': '0.00',
        // 'a13_1': '0.00', 'a13_2': '0.00', 'a13_3': '0.00', 'a13_4': '0.00',
        // 'a14_1': '0.00', 'a14_2': '0.00', 'a14_3': '0.00', 'a14_4': '0.00',
        // 'a15_1': '0.00', 'a15_2': '0.00', 'a15_3': '0.00', 'a15_4': '0.00',
        // 'a16_1': '0.00', 'a16_2': '0.00', 'a16_3': '0.00', 'a16_4': '0.00',
        // 'a17_1': '0.00', 'a17_2': '0.00', 'a17_3': '0.00', 'a17_4': '0.00',
        // 'a18_1': '0.00', 'a18_2': '0.00', 'a18_3': '0.00', 'a18_4': '0.00',
        // 'a19_1': '0.00', 'a19_2': '0.00', 'a19_3': '0.00', 'a19_4': '0.00',
        // 'a21_1': '0.00', 'a21_2': '0.00', 'a21_3': '0.00', 'a21_4': '0.00'
      },
      com: []
    }
  },
  computed: {
    formData () {
      return this.$store.state.tableData[this.name]
    }
  },
  mounted () {
    this.inputChangeEvent()
    this.inputClickEvent()
    this.getSetting()  //获取后台设置数据
    this.formrevise()  //获取表间数据
  },
  methods: {
    getSetting () {
      if (this.formData) {
        this.recoverFormData()
      }
      // axios.get('getSetting').then(res => {})
      //获取预设数据
      this.com = this.$store.state.companyCurrent.gscompany
      // this.setting = this.$store.state.companyCurrent.content.page26
      // this.initData()
    },
    initData () {
      this.$Loading.start()
      let that = this
      for (let key in this.setting) {
        J('input').each(function () {
          if (J(this).attr('name') === key) {
            J(this).val((that.setting[key] * 1).toFixed(2))
            J(this).trigger('blur')
            return false
          }
        })
      }
      this.$Loading.finish()
    },
    recoverFormData () {
      this.$Loading.start()
      let that = this
      J('input').each(function () {
        let nm = J(this).attr('name')

        that.formData.map(v => {
          if (v.name === nm) {
            J(this).val(v.value)
          }
        })
      })
      this.$Loading.finish()
    },
    fn () {

      //17行1、3列公式
      // this.fn2(1, 17, [1], [12, 13, 14, 15, 16], 17, 1)
      // this.fn2(3, 17, [3], [12, 13, 14, 15, 16], 17, 1)
      // //18行公式
      // this.fn3(4, 18)
      // //19、20、24、27、32、33、34、38行公式
      // this.fn4(19, 4, [11, 18], 1)
      // this.fn4(20, 4, [17, 18], 1)
      // this.fn4(24, 4, [19, 21, 23], 2)
      // this.fn4(27, 4, [28, 29, 30, 31], 3)
      // this.fn4(32, 4, [24, 25, 26, 27], 4)
      // this.fn4(33, 4, [25, 26, 27], 2)
      // this.fn4(34, 4, [24, 28, 29], 5)
      // this.fn4(38, 4, [16, 22, 36, 37], 4)
      // //本年累计
      // this.fn1(2, 38, [1])
      // this.fn1(4, 38, [3])

      this.fn4(9, 1, [10, 11, 12], 3) // 计算第9行第1列数据
      this.fn4(9, 2, [10, 11, 12], 3)
      this.fn4(9, 3, [10, 11, 12], 3)
      this.fn4(9, 4, [10, 11, 12], 3)

      this.fn4(20, 1, [15, 16], 1)
      this.fn4(20, 2, [15, 16], 1)

      this.fn4(22, 1, [20, 21], 1)
      this.fn4(22, 2, [20, 21], 1)
    },
    //(
    // q  要修改的值列
    // r  要修改多少行
    // seed 每行参与计算的列
    // {
    //    1加 a=a+b
    // }
    // )
    fn1 (q, r, seed) {
      for (let i = 1; i <= r; i++) {
        var sum = 0
        for (let j = 0; j < seed.length; j++) {
          let m = seed[j]

          let dom = J('input[name=' + i + '_' + m + ']')
          if (dom.val()) {
            if (this.$store.state.companyCurrent.content.page27[i + '_2']) {
              sum += parseFloat(1 * (dom.val()) + 1 * (this.$store.state.companyCurrent.content.page27[i + '_2']))
            } else {
              sum += parseFloat(1 * (dom.val()) + 0)
            }
          }
        }
        J('input[name=' + i + '_' + q + ']').val(sum.toFixed(2))
      }
    },
    //(
    // q  要修改的值列
    // r  要运算多少行
    // seed 每行参与计算的列
    // rows 参与运算的行数
    // gps  需要运算的行
    // type {
    //       1:a=a+b-c-d+e(12+13-14-15+16)
    //      }
    //)
    fn2 (q, r, seed, rows, gps, type) {
      var sum = 0
      for (let i = 1; i <= r; i++) {
        for (let j = 0; j < seed.length; j++) {
          let m = seed[j]
          let dom = J('input[name=' + i + '_' + m + ']')
          if (dom.val()) {
            if (J.inArray(i, rows) >= 0) {
              if (!isNaN(dom.val())) {
                if (type == 1) {
                  let vl = 0
                  if (i == 14 || i == 15) {
                    sum = parseFloat(sum - dom.val())
                  } else {
                    sum += parseFloat(dom.val())
                  }
                }
              }
            }
          }
        }
      }
      J('input[name=' + gps + '_' + q + ']').val(sum.toFixed(2))
    },
    //q   一共要比对多少列
    //gps 需要运算的行
    //比较公式  如果17行<11行 q=17 ,否则q=11
    fn3 (q, gps) {
      for (let i = 1; i <= q; i++) {

        if (J('input[name=11_' + i + ']').val()) {
          var dom1 = 1 * (J('input[name=11_' + i + ']').val())
        } else {
          var dom1 = 0
        }
        if (J('input[name=17_' + i + ']').val()) {
          var dom2 = 1 * (J('input[name=17_' + i + ']').val())
        } else {
          var dom2 = 0
        }
        if (dom2 < dom1) {
          J('input[name=' + gps + '_' + i + ']').val(dom2.toFixed(2))
        } else {
          J('input[name=' + gps + '_' + i + ']').val(dom1.toFixed(2))
        }
      }
    },
    //q     要修改那一行
    //line  要修改多少列
    // seed 参与计算的行
    //type  {
    //        1:a=b-c
    //        2:a=b+c-d
    //        3:a=b+c+d...
    //        4:a=b+c+d-e
    //        5:a=b-c-d...
    //      }
    fn4 (q, line, seed, type) {
      for (let i = 1; i <= line; i++) {
        var sum = 0
        for (let j = 0; j < seed.length; j++) {
          let m = seed[j]
          let dom = J('input[name=' + m + '_' + i + ']')

          if (dom.val()) {
            if (!isNaN(dom.val())) {
              if (type == 1) {
                if (j == 0) {
                  sum = dom.val()
                } else {
                  sum -= parseFloat(dom.val())
                }
              } else if (type == 2) {
                if (j == 2) {
                  sum -= parseFloat(dom.val())
                } else {
                  sum += parseFloat(dom.val())
                }
              } else if (type == 3) {
                sum += parseFloat(dom.val())
              } else if (type == 4) {
                if (j == 3) {
                  sum -= parseFloat(dom.val())
                } else {
                  if (j == 0) {
                    sum = parseFloat(dom.val())
                  } else {
                    sum += parseFloat(dom.val())
                  }
                }
              } else if (type == 5) {
                if (j == 0) {
                  sum = parseFloat(dom.val())
                } else {
                  sum -= parseFloat(dom.val())
                }
              }
            } else {
              console.log('error')
            }
          }
        }
        J('input[name=' + q + '_' + i + ']').val((sum * 1).toFixed(2))
      }
    },
    formrevise () {
      var name1 = ''
      var name2 = ''

      if (this.$store.state.tableData[27]) {
        //附列资料二    12行第3列
        // let val12_1 = 1 * (this.$store.state.tableData[24][32].value)
        // //13行
        // let val1_4 = 1 * (this.$store.state.tableData[24][31].value)
        // this.form.a12_1 = val12_1.toFixed(2)
        // this.form.a14_1 = val1_4.toFixed(2)
        // 获取第一行第二列数据

        let val1_2 = 1 * (this.$store.state.tableData[27][7].value)
        this.form.a1_2 = val1_2.toFixed(2)
      } else {
        name1 = '《增值税纳税申报表(适用于增值税小规模纳税人)附列资料》'
      }
      if (this.$store.state.tableData[44]) {
        //附列资料一  第9列的1-5行(包含4a行 4b行)6、7、16、17行
        // let val1_9 = 1 * (this.$store.state.tableData[22][8].value)
        // let val2_9 = 1 * (this.$store.state.tableData[22][18].value)
        // let val4a_9 = 1 * (this.$store.state.tableData[22][46].value)
        // let val4b_9 = 1 * (this.$store.state.tableData[22][56].value)
        // let val5_9 = 1 * (this.$store.state.tableData[22][70].value)
        // let val6_9 = 1 * (this.$store.state.tableData[22][76].value)
        // let val7_9 = 1 * (this.$store.state.tableData[22][78].value)
        // let val16_9 = 1 * (this.$store.state.tableData[22][186].value)
        // let val17_9 = 1 * (this.$store.state.tableData[22][189].value)

        // let val18_9 = 0
        // if (this.$store.state.tableData[22][197] && this.$store.state.tableData[22][197].value) {
        //   val18_9 = 1*(this.$store.state.tableData[22][197].value)
        // }

        // let val19_9 = 0
        // if (this.$store.state.tableData[22][197] && this.$store.state.tableData[22][197].value) {
        //   val19_9 = 1*(this.$store.state.tableData[22][200].value)
        // }
        // //第7列的1-5行(包含4a行 4b行)
        // let val1_7 = 1 * (this.$store.state.tableData[22][6].value)
        // let val2_7 = 1 * (this.$store.state.tableData[22][16].value)
        // let val4a_7 = 1 * (this.$store.state.tableData[22][44].value)
        // let val4b_7 = 1 * (this.$store.state.tableData[22][54].value)
        // let val5_7 = 1 * (this.$store.state.tableData[22][68].value)
        // //第10列 1、3、4a、6、8、9a、10、11、14行
        // let val1_10 = 1 * (this.$store.state.tableData[22][9].value)
        // let val2_10 = 1 * (this.$store.state.tableData[22][19].value)
        // let val4a_10 = 1 * (this.$store.state.tableData[22][47].value)
        // let val4b_10 = 1 * (this.$store.state.tableData[22][57].value)
        // let val6_10 = 1 * (this.$store.state.tableData[22][77].value)
        // let val8_10 = 1 * (this.$store.state.tableData[22][91].value)
        // let val9a_10 = 1 * (this.$store.state.tableData[22][99].value)
        // let val10_10 = 1 * (this.$store.state.tableData[22][119].value)
        // let val11_10 = 1 * (this.$store.state.tableData[22][127].value)
        // let val14_10 = 1 * (this.$store.state.tableData[22][177].value)
        // //第14列2、4b、5、7、9b、12、13a、13b、15行
        // let val2_14 = 1 * (this.$store.state.tableData[22][23].value)
        // let val4b_14 = 1 * (this.$store.state.tableData[22][61].value)
        // let val5_14 = 1 * (this.$store.state.tableData[22][75].value)
        // let val7_14 = 1 * (this.$store.state.tableData[22][83].value)
        // let val9b_14 = 1 * (this.$store.state.tableData[22][111].value)
        // let val12_14 = 1 * (this.$store.state.tableData[22][131].value)
        // let val13a_14 = 1 * (this.$store.state.tableData[22][151].value)
        // let val13b_14 = 1 * (this.$store.state.tableData[22][155].value)
        // let val15_14 = 1 * (this.$store.state.tableData[22][183].value)

        // let val3_10 = 1 * (this.$store.state.tableData[22][33].value)
        // let val4_10 = 1 * (this.$store.state.tableData[22][35].value)
        // let val5_10 = 1 * (this.$store.state.tableData[22][49].value)
        // //第9列1-5行之和
        // this.form.a1_1 = (val1_9 + val2_9 + val4a_9 + val4b_9 + val5_9).toFixed(2)
        // //第7列1-5行之和
        // this.form.a4_1 = (val1_7 + val2_7 + val4a_7 + val4b_7 + val5_7).toFixed(2)
        // //第9列16+17行
        // this.form.a7_1 = (val16_9 + val17_9).toFixed(2)
        // //第9列18+19行
        // this.form.a8_1 = (val18_9 + val19_9).toFixed(2)
        // //第10列
        // this.form.a11_1 = (val1_10 + val2_10 + val3_10 + val4_10 + val5_10).toFixed(2)

        // //第10列8+9a+10+11-14+第14列9b+12+13a+13b-15
        // this.form.a21_1 = (val8_10 + val9a_10 + val10_10 + val11_10 - val14_10 + val9b_14 + val12_14 + val13a_14 + val13b_14 - val15_14).toFixed(2)
        // //第9列6+7
        // this.form.a1_3 = (val6_9 + val7_9).toFixed(2)
        // //第10列6行+第14列7行
        // this.form.a11_3 = (val6_10 + val7_14).toFixed(2)
        // //第10列14行+第14列15行
        // this.form.a21_1 = (val14_10 + val15_14).toFixed(2)
      } else {
        name2 = '《增值税减免税申报明细表(季度)》'
      }
      if (name1 != '' || name2 != '') {
        this.eg('请按照填表顺序正确填写:' + name1 + name2 + '表未填写！')
      }
    }
  }
}
</script>

<style scoped>
.p2 {
  width: 1200px;
  margin: 0px auto;
}

table,
table tr th,
table tr td {
  font-family: "宋体";
  border: 1px solid #333;
}

table {
  max-width: 100%;
  min-height: 25px;
  line-height: 25px;
  text-align: center;
  border-collapse: collapse;
  padding: 2px;
}

input,
select {
  width: 100%;
  height: 100%;
  border: none;
  background: transparent;
  padding: 0 2px;
  outline: 0px;
  text-align: right;
}

td {
  text-align: center;
  padding: 3px 2px;
}

.bg-write {
  background: #d6ebff;
}

.bg-unwrite {
  background: #ccccff;
}

.tdleft {
  text-align: left;
}
.t_l {
  text-align: left;
}

.modSty {
  background-color: #d6ebff;
  height: 16px;
}
</style>



